import 'regenerator-runtime';
import '../styles/main.css';
import swRegister from './sw-register';

document.addEventListener('DOMContentLoaded', () => {
  const skipToContent = document.querySelector('.skip-to-content');
  const mainContent = document.querySelector('#main-content');

  skipToContent.classList.remove('focus-visible');

  window.addEventListener('keydown', (event) => {
    if (event.key === 'Tab' && !skipToContent.classList.contains('focus-visible')) {
      skipToContent.classList.add('focus-visible');
    }
  });

  skipToContent.addEventListener('focusout', () => {
    skipToContent.classList.remove('focus-visible');
  });

  skipToContent.addEventListener('click', (event) => {      
    event.preventDefault();
    mainContent.focus();
});

  const menuToggle = document.querySelector('.menu-toggle');
  const drawer = document.querySelector('.navbar .nav-menu');

  menuToggle.addEventListener('click', (event) => {
    toggleMenu(event);
  });

  menuToggle.addEventListener('keydown', (event) => {
    if (event.key === 'Enter') {
      toggleMenu(event);
    }
  });

  function toggleMenu(event) {
    if (event.type === 'click' || (event.type === 'keydown' && event.key === 'Enter')) {
      drawer.classList.toggle('open');
      const expanded = drawer.classList.contains('open');
      menuToggle.setAttribute('aria-expanded', expanded.toString());
    }
  }

  const homeLink = document.getElementById('home-link');
  const favoriteLink = document.getElementById('favorite-link');

  homeLink.addEventListener('click', async (event) => {
    event.preventDefault();
    const { loadHomePage } = await import('./home');
    loadHomePage();
  });

  favoriteLink.addEventListener('click', async (event) => {
    event.preventDefault();
    const { loadFavoritePage } = await import('./favorite');
    loadFavoritePage();
  });

  swRegister()

  let deferredPrompt;
  const addBtn = document.createElement('button');
  addBtn.textContent = 'Add to Home Screen';
  addBtn.style.display = 'none';
  document.body.appendChild(addBtn);

  window.addEventListener('beforeinstallprompt', (e) => {
    e.preventDefault();
    deferredPrompt = e;
    addBtn.style.display = 'block';

    addBtn.addEventListener('click', (e) => {
      addBtn.style.display = 'none';
      deferredPrompt.prompt();
      deferredPrompt.userChoice.then((choiceResult) => {
        if (choiceResult.outcome === 'accepted') {
          console.log('User accepted the A2HS prompt');
        } else {
          console.log('User dismissed the A2HS prompt');
        }
        deferredPrompt = null;
      });
    });
  });

  (async () => {
    const { loadHomePage } = await import('./home');
    loadHomePage();
  })();
});
